.content.youtube {
	.cate {
		width: 100%;
		margin: 0 auto;
		position: absolute;
		top: -25vmin;
		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 2px;
			background-color: #333;
			position: absolute;
			bottom: -2vmin;
		}
		span {
			display: inline-block;
			width: 50%;
			text-align: center;
			font: bold 3vmin 'Poppins';
			cursor: pointer;
		}
	}

	.inner {
		width: 1180px;
		margin: 35vmin auto;
		position: relative;
		.list {
			width: 100%;
			position: relative;
			.youtube {
				width: 100%;
				position: relative;
				.sponsorship {
					.main {
						display: flex;
						margin: 30px 0;
						cursor: pointer;
						img {
							width: 40%;
							height: 400px;
							object-fit: cover;
							margin-right: 10%;
						}
						.txt {
							width: 50%;
							height: 400px;
							> h1 {
								font: bold 5vmin 'Poppins';
								margin-top: 50px;
								margin-bottom: 4vmin;
							}
							h2 {
								font: bold 2vmin 'Poppins';
								position: absolute;
								left: 50%;
								top: 3vmin;
							}
							> p {
								font: 3vmin/1.5 'Lora';
							}
						}
					}
					.videos {
						width: 100%;
						margin-bottom: 25vmin;
						cursor: pointer;
						img {
							width: 25%;
							height: 250px;
							padding: 10px;
						}
					}
					.frame {
						width: 100%;
						position: relative;
						span {
							font: italic 4vmin/1 'Lora';
							position: absolute;
							top: -6vmin;
							right: 0;
							&::before {
								content: '';
								display: block;
								width: 150%;
								height: 2px;
								background-color: #333;
								position: absolute;
								right: 0;
								top: -1vmin;
							}
						}
						.pics {
							width: 100%;
							overflow: hidden;
							.slide {
								width: calc((1180px / 3) * 7);
								img {
									width: calc(1180px / 3);
									height: 300px;
									padding: 0 5px;
								}
							}
							.play,
							.stop {
								position: absolute;
								top: 50%;
								left: 50%;
								transform: translate(-50%, -50%);
								display: none;
								font: bold 7vmin/1 'Poppins';
								color: #fff;
							}
						}
						.prev {
							position: absolute;
							top: 50%;
							left: -10%;
							transform: translate(0%, -50%);
							color: #666;
							font: bold 5vmin/1 'Poppins';
						}
						.next {
							position: absolute;
							top: 50%;
							right: -10%;
							transform: translate(0%, -50%);
							color: #666;
							font: bold 5vmin/1 'Poppins';
						}
					}
				}
				.advertisement {
					width: 100%;
					&::after {
						content: '';
						display: block;
						clear: both;
					}
					article {
						width: 50%;
						float: left;
						display: block;
						height: 60vmin;
						padding: 0 1%;
						position: relative;
						margin-bottom: 20vmin;
						&:nth-of-type(odd) {
							&::after {
								content: '';
								display: block;
								width: 90%;
								margin: 0 auto;
								height: 1px;
								background-color: #333;
								margin-top: 10vmin;
							}
						}
						&:nth-of-type(2n) {
							margin-top: 0px;
							p {
								position: absolute;
								top: 5%;
							}
							&::before {
								content: '';
								display: block;
								width: 90%;
								margin: 0 auto;
								height: 1px;
								background-color: #333;
								margin-bottom: 10vmin;
							}
						}
						img {
							width: 100%;
							height: 80%;
						}
						> p {
							width: 90%;
							height: 20%;
							font: 2vmin/1 'Poppins';
							position: absolute;
							bottom: -5%;
							left: 5%;
							text-align: center;
						}
					}
				}
			}
		}
	}
}

@media screen and(max-width:1179px) {
	.content.youtube {
		figure {
			display: none;
		}
		.inner {
			width: 100%;
			padding-top: 12vmin;
			.list {
				width: 100%;
				position: relative;
				> article {
					display: inline-block;
					position: relative;
					width: 50%;
					height: 50vmin;
					text-align: center;
					background: none;
					padding: 0;
					margin-right: 0;
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
					> P {
						position: absolute;
						bottom: 5%;
						left: 5%;
						color: #fff;
						font: 3vmin 'arial';
					}
				}
				.data {
					width: 100%;
					margin-top: 10vmin;
					margin-bottom: 20vmin;
					> article {
						display: inline-block;
						width: 33%;
						height: 35vmin;
						margin-right: 0.5%;
						margin-top: 10%;
						border-radius: 5px;
						&:nth-of-type(3n) {
							margin-right: 0;
						}
						img {
							width: 100%;
							height: 100%;
							object-fit: cover;
						}
					}
				}
			}
		}
	}
}
