.mapCompo {
	width: 1180px;
	margin: 30vmin auto;
	> span {
		font: bold 4vmin/1 'Poppins';
	}
	.wrap {
		width: 100%;
		position: relative;
		margin-top: 5vmin;
		font-size: 0;
		#map1 {
			display: inline-block;
			width: 55%;
			height: 400px;
			filter: grayscale(0.7);
			z-index: 2;
		}
		.txt {
			text-align: center;
			display: inline-block;
			width: 55%;
			height: calc(400px + 4vmin);
			background: #222;
			color: #ddd;
			position: absolute;
			top: 4vmin;
			right: 0%;
			font: 2vmin/1 'Poppins';
			h1 {
				margin-top: 10%;
				margin-left: 5vmin;
			}
			form {
				width: 50%;
				margin: 0 auto;
				fieldset {
					border: none;
				}
				.h {
					opacity: 0;
				}
				input[type='text'] {
					width: 120%;
					height: 4vmin;
					font: 1.5vmin/1 'Poppins';
					margin: 1vmin 0;
				}
				select {
					margin-left: 5vmin;
					margin-top: 1vmin;
				}
				input[type='submit'] {
					margin-top: 10%;
					width: 35vmin;
					height: 5vmin;
					margin-left: 3vmin;
				}
			}
		}
	}
	.btnBox {
		width: 55%;
		text-align: left;
		position: relative;
		z-index: 2;
		button {
			width: 25%;
			height: 5vmin;
			border: none;
			font: 1.5vmin/2 'Poppins';
			cursor: pointer;
			border-right: rgb(255, 255, 255) solid 1px;
		}
	}
}
